.UI {
    text-align: center;
    button {
        border: 0;
        border-radius: 4px;
        padding: 0 40px;
        height: 45px;
        line-height: 45px;
        background-color: transparent;
        box-shadow: inset 0 0 0 1px #dee1e3;
        color: #727a82;
        font-family: "Montserrat", sans-serif;
        font-weight: 800; }
    button {
      &:hover {
        outline: none; } } }
