.SignUp {
    position: absolute;
    top: 55%;
    left: 50%;
    background: rgba(255, 255, 255, 0.75);
    margin: 0 auto;
    transform: translate(-50%, -50%);
    border-radius: 5px;
    .SignUpInputs {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        input {
            outline: none;
            min-width: 320px;
            height: 60px;
            margin: 10px 0px; } }
    .SignInHaveAccountHint {
        text-align: center;
        color: black;
        font-family: "Montserrat", sans-serif; }
    .SignUpButton {
        width: 320px; }
    .SignInButton {
        button {
            color: blue;
            border-width: 0;
            box-shadow: none;
            font-size: 80; } } }

